/*
 *  Login Form
 *
 *  Version:
 *    1.0.0 - 2020/11/04
 */

.c-login-form {
  display: none;

  &__title {
    margin-bottom: t-to-rem(20);
  }

  &__btn {
    margin-top: t-to-rem(32);
  }

  .o-form__input-group {
    margin: t-to-rem(16) 0;
  }

  &.is-visible {
    display: block;
  }

  &__forgot {
    display: block;
    margin: t-to-rem(16) 0;
  }

  &__title.is-guest {
    margin-top: 3rem;
  }

  @include t-media-query($s-medium-down) {
    padding-top: t-to-rem(40);
    border-top: 1px solid rgba($s-color--background-secondary, 0.1);
    margin-top: t-to-rem(40);
  }

  @include t-media-query($s-medium-up) {
    margin-top: 0;
    padding-top: 0;
    border-top: none;
  }
}