/*
 *  Activate Account
 */

.c-activate-account-template {
  position: relative;
  overflow: visible;

  .errors {
    text-align: left;
    color: $s-color--error;

    &>ul {
      margin-top: 0;
    }
  }

  .o-input {
    margin-bottom: 1rem;
  }

  &__input {
    border-radius: 1.5625rem;
    padding-right: 5.625rem;
  }

  &__row {
    display: flex;
    flex-direction: column;
    margin-top: 1rem;
  }

  &__btn {
    &.is-left {
      margin-bottom: .5rem;
    }
  }

  &__form {
    &-btns {
      display: flex;
      flex-direction: column;
      margin-top: 2.0625rem;
    }

    &-btn {
      margin-bottom: 1.25rem;
    }
  }

  @include t-media-query($s-small-up) {

    &__row {
      flex-direction: row;
      margin-top: 1rem;
      justify-content: center;
    }

    &__btn {
      &.is-left {
        margin-right: .5rem;
      }

      &.is-right {
        margin-left: .5rem;
      }
    }

    &__form-btns {
      max-width: 15.4375rem;
    }
  }

  @include t-media-query($s-medium-up) {
    margin: 0 auto;
    text-align: center;

    &__input {
      padding: 0.5rem 4.375rem 0.5rem 1.25rem;
      font-size: 0.625rem;
      letter-spacing: 0.05em;
    }

    &__form {
      width: 50%;
      margin: 0 auto;

      &-btns {
        display: flex;
        flex-direction: row;
        margin: 2.0625rem auto 0;
        max-width: 100%;

      }

      &-btn {
        margin-bottom: 0;
        margin-right: 1.25rem;
      }
    }
  }

  @include t-media-query($s-large-up) {

    &__input {
      font-size: 0.875rem;
      line-height: 1.45;
      letter-spacing: 0.04357em;
      padding: 0.9rem 1.25rem;
    }

    &__row {
      margin-top: 2rem;
    }
  }
}