 *    1.0.0 - 2020/11/04
 */


.c-login-register {
  margin: 1.5rem auto;
  padding: 0 1.5rem;

  .o-btn:not(.is-guest) {
    margin-top: 1rem;
  }

  .errors {
    text-align: left;
    color: $s-color--error;

    &>ul {
      margin: 0;
      padding-left: 1.4rem;
    }
  }

  &__input {
    padding-right: 5.625rem;
    color: $s-color--font-1;
    border-bottom: 1px solid $s-color--font-4;
    padding-bottom: t-to-rem(16);

    &::placeholder {
      color: $s-color--grey-blue;
      font-style: italic;
    }
  }

  &__errors {
    margin-bottom: 1rem;
  }

  &__form-container {
    &.is-error-form {
      width: 100%;
      display: block;
    }
  }

  &__toggle-wrapper {
    margin-top: 2.5rem;

    &.is-new-password {
      display: none;
    }
  }

  &__login-toggle {
    text-decoration: underline;
  }

  @include t-media-query($s-medium-up) {
    margin: 2.5rem auto;
    padding: 0 3rem;

    &__errors {
      display: flex;
      flex-direction: column;
      align-items: center;
      margin-bottom: 2rem;
    }

    &__form {

      &-container {

        &.is-login,
        &.is-register {
          width: 60%;
        }
      }
    }

    &__input {
      padding: 0.5rem 4.375rem 0.5rem 0;
    }

    .o-btn:not(.is-guest) {
      margin-top: 2rem;
    }
  }

  @include t-media-query($s-large-up) {
    margin: 9rem auto;
    padding: 0 10rem;
    max-width: 100rem;

    &__input {
      padding: 0.9rem 1.25rem 0.9rem 0;
    }
  }
}