.c-account-navigation {

  &__heading {
    margin-bottom: t-to-rem(20);
  }

  &__list {
    &-item {
      margin-bottom: t-to-rem(16);

      &:last-child {
        margin-bottom: unset;
      }
    }
  }

  /*==== Media Queries ====*/
  @include t-media-query($s-medium-down) {
    padding-bottom: t-to-rem(32);
    border-bottom: 2px solid rgba($s-color--background-secondary, 0.1);
    margin-bottom: t-to-rem(32);
  }

  @include t-media-query($s-medium-up) {

    &__heading {
      margin-bottom: t-to-rem(24);
    }

    &__list {
      &-item {
        margin-bottom: t-to-rem(20);
      }
    }
  }
}