/*
 *  Base Colors
 *  - For base color naming conventions use: https://chir.ag/projects/name-that-color
 *  - These base colors should only ever be used inside this color file to assign to
 *    other color variables
 */
$s-color--black: #000;
$s-color--white: #FFFFFF;
$s-color--red: #FF0000;
$s-color--grey: #ccc;
$s-color--clear: transparent;

$s-color--blue-green: #004851;
$s-color--aqua: #A6D6DE;
$s-color--stone-blue: #7FA9AE;
$s-color--tower-gray: #AFBFC4;
$s-color--aqua-haze: #F0F6F7;
$s-color--mint-tulip: #B4F1EA;

$s-color--pale-blue: #BDD0D5;
$s-color--grey-blue: #83A5AC;
$s-color--pale-grey: #F0F0F0;
$s-color--pale-grey-blue: #E5F0F2;
$s-color--dark-blue: #1C4550;
$s-color--light-aqua: #C8EEF4;
$s-color--grey-green: #779196;
$s-color--light-blue: #F1F7F8;
$s-color--phosphorescence: #00FFEE;

/*
 *  Color Definitions
 */

// Backgrounds
$s-color--background:$s-color--white;
$s-color--background-secondary: $s-color--blue-green;
$s-color--background-tertiary: $s-color--aqua;
$s-color--background-quaternary: $s-color--pale-grey-blue;
$s-color--background-quinary: $s-color--light-aqua;
$s-color--background-senary: $s-color--stone-blue;
$s-color--background-septenary: $s-color--light-blue;
$s-color--background-octonary: $s-color--mint-tulip;
$s-color--disabled-grey: $s-color--grey;

// Fonts
$s-color--font-1: $s-color--blue-green;
$s-color--font-2: $s-color--aqua;
$s-color--font-3: $s-color--stone-blue;
$s-color--font-4: $s-color--pale-blue;
$s-color--font-5: $s-color--grey-blue;
$s-color--font-6: $s-color--white;
$s-color--font-8: $s-color--grey-green;
$s-color--font-9: $s-color--mint-tulip;
$s-color--font-10: $s-color--phosphorescence;

// Borders
$s-color--border: $s-color--black;
$s-color--border-focus: $s-color--grey-blue;
$s-color--border-secondary: $s-color--aqua;

// Buttons
$s-color--btn-font: $s-color--white;
$s-color--btn-background: $s-color--black;

// Input
$s-color--error: $s-color--red;