.c-register-form {
  &__title {
    margin-bottom: t-to-rem(20);
  }

  .o-form__input-group {
    margin: t-to-rem(16) 0;
  }

  &__btn {
    margin-top: t-to-rem(32);
  }

  &__error {
    display: none;
    color: $s-color--error;

    &.is-visible {
      display: block;
    }
  }
  .form-message{

    border: 1px solid $s-color--border-secondary;
    margin: 2rem 0;
    padding: 2rem;

  }
}